<template>
    <div class="TakeOut">
         <!-- 头部 -->
        <Header></Header>
        <!-- 轮播 -->
        <content>
            <!-- 上拉刷新 -->
            <van-pull-refresh v-model="isLoading" @refresh="onRefresh">
                <!-- 下拉下载 -->
                <van-list v-model="loading" finished-text="没有更多了" @load="onLoad" >
            <!-- <p>刷新次数: {{ count }}</p> -->

            <div class="block">
                <div class="swiper-container">
                    <div class="swiper-wrapper">
                        <div class="swiper-slide">
                            <ul>
                                <li v-for="(v,i) in arr" :key="i">
                                <img :src="'https://fuss10.elemecdn.com'+v.image_url">
                                <p>{{v.title}}</p >
                                </li>
                            </ul>
                        </div>
                    
                        <div class="swiper-slide">
                            <ul >
                                <li v-for="(item,i) in list" :key="i">
                                <img :src="'https://fuss10.elemecdn.com'+item.image_url">
                                <p>{{item.title}}</p >
                                </li>
                            </ul>
                        </div>
                    </div>
                        <div class="swiper-pagination"></div>
                </div>
        </div>
        <!-- 商品列表 -->
        <div class="fu">
            <i class="el-icon-sold-out"></i>
            <span class="sp">附近商家</span>
            <div class="fu_yi">
                    <ul v-for='(v,i) in info' :key="i" @click="producelist(v)">
                        <li><img :src="`http://elm.cangdu.org/img/${v.image_path}`" alt=""></li>
                        <li>
                            <div>
                                <p class="Brand_line">
                                    <span class="brand">品牌</span>
                                    <span class="result">{{v.name}}</span>
                                </p>
                                <p class="you">
                                    <span class="ticket">保准票</span>
                                </p>
                            </div>
                            <div>
                                <p class="sales_line">
                                    <span class="star">★★★★{{v.rating}}</span>
                                    <span class="work">月售{{v.recent_order_num}}单</span>
                                </p>
                                <p class="present">
                                    <span class="hummingbird">蜂鸟专送</span>
                                    <span class="Jusda">准时达</span>
                                </p>
                            </div>
                            <div>
                                <p class="match">
                                    <span class="dispatching">￥{{v.float_minimum_order_amount}}起送/配送费约￥{{v.float_delivery_fee}}</span>
                                    <span class="duration">
                                        <span class="kilometre">{{v.distance}}/</span>
                                        <span class="minute">{{v.order_lead_time}}</span>
                                    </span>
                                </p>
                            </div>
                        </li>
                    </ul>
                </div>
                 </div>
             </van-list>                 
         <!-- 加载中 -->
        </van-pull-refresh>
    </content>
    <Footer></Footer>
    </div>
</template>
<script>
import Header from "@/page/City/Header"
import axios from "axios"
import Footer from "@/page/Footer/Footer"
export default {
    components:{
        Header,
        Footer
    },
   data() {
      return {
          info:[],
        //   轮播1页
          arr:[],
          arr2:[],
        //   轮播2页
          list:[],
          value5: 0,
        //   下拉刷新
          count:0,
          isLoading:false,
        //   商品加载
            loading: false,
            finished: false,
            offset:0
      }
   },
   created() {
    axios.get("http://elm.cangdu.org/shopping/restaurants?latitude=45.74764&longitude=126.63249&offset=0&limit=10&extras[]=activities&keyword=&restaurant_category_id=&restaurant_category_ids[]=&order_by=&delivery_mode[]=").then((res)=>{
        //    console.log(res.data)
           this.info=res.data
       })
       axios.get("http://elm.cangdu.org/v2/index_entry?geohash=39.78493,116.49476&group_type=1&flags[]=F").then((ree)=>{
           this.arr=ree.data
           var a=ree.data.splice(8,15)
           this.list=a
        //    console.log(this.list)
       })
   },
   mounted() {
        new Swiper(".swiper-container",{
            autoplay:2000,
            //observer:true,//修改swiper自己或子元素时，自动初始化swiper
            //observeParents:true,//修改swiper的父元素时，自动初始化swiper
            pagination:'.swiper-pagination'
        })
   },
   methods: {
    //    上拉刷新
       onRefresh(){
           setTimeout(()=>{
               this.$toast('刷新成功');
               this.isLoading=false;
               this.count++;
           },300)
       },
    //    商品加载
        onLoad() {
      // 异步更新数据
      setTimeout(() => {
          this.offset+=10
            axios.get(`http://elm.cangdu.org/shopping/restaurants?latitude=45.74764&longitude=126.63249&offset=${this.offset}&limit=10&extras[]=activities&keyword=&restaurant_category_id=&restaurant_category_ids[]=&order_by=&delivery_mode[]=`).then((res)=>{
        //    console.log(res)
           this.info=this.info.concat(res.data)
       })
        // 加载状态结束
        this.loading = false;
       }, 1000);
    },
    producelist(v){
        // this.axios.get(`http://elm.cangdu.org/ugc/v2/restaurants/1/ratings?has_content=true&offset=0&limit=10&tag_name=${}`).then((res)=>{
        //     console.log(res.data)
        // })
        this.$router.push({
            path:'/home/product',
            // name:'Product',
            query:{
                v:v
            }
        })
        
    }
   }
}
</script>

<style lang="scss">
*{
    margin: 0;
    padding: 0;
}
body{
    background: #F5F6F5;font-size: 0.26rem;
}
.box{
    width: 100%;height: 100%;display: flex;flex-direction: column;
    content{
        margin-top:0.9rem;
        .van-list{
            margin-top: -0.25rem;
        }
    }
}
.block{
    width:100%;background: white;margin-top: 1rem;
    border-bottom: 2px solid rgb(233, 232, 232);
    .swiper-container{
        width:100%;height: 2.8rem;overflow: hidden;
        .swiper-wrapper{
            width:200%;display: flex;
        }
        .swiper-slide{
            width:100%;height: 100%;
            ul{
                margin: 0;padding: 0;height:2.8rem;
                display: flex;flex-wrap: wrap;
                li{
                    margin: 0;padding: 0;list-style: none;width: 25%;height: 50%;display: flex;
                    flex-direction: column;justify-content: center;align-items: center;overflow: hidden;
                    img{
                        height: 50%;width: 40%;
                    } 
                }
            }
        }
   
  }
}
.fu{
    width: 100%;background: white;
    margin-top: 0.2rem;
    // border-top: 5px solid rgb(233, 232, 232);
    margin-bottom: 0.2rem;
    i{
        color: #B0B0B0;display: block;padding-left: 0.2rem;font-size: 0.31rem;
    }
    .sp{
        font-size: 0.25rem;display: block;color: #B0B0B0;
        padding-left: 0.55rem;margin-top: -0.3rem;
    }
  } 
  .fu_yi{
    width: 100%;display: flex;flex-wrap: wrap;margin-top: 0.15rem;
    ul{
        width: 100%;display: flex;margin-top: 0.35rem;
        border-bottom: 2px solid rgb(233, 232, 232);
        li:nth-child(1){
            img{
                width: 1.35rem;height: 1.35rem;margin-left: 0.2rem;
            }
        }
        li:nth-child(2){
            width: 100%;
            line-height: 0.45rem;
            div{
                display: flex;justify-content:space-between;margin-left: 0.15rem;
                .Brand_line{
                    .brand{
                        background:#F9DA3E; font-size: 0.2rem;
                    }
                    .result{
                        font-size: 0.2rem;
                    }
                }
                .you{
                    .ticket{
                        color: gray;font-size: 0.25rem;
                    }
                }
                .sales_line{
                    .star{
                        color: #FC9A1E;
                    }
                    .work{
                        font-size: 0.2rem;color: gray;
                    }
                }
                .present{
                    .hummingbird{
                        font-size: 0.18rem;background:#2E8EE2; 
                        color: white;border-radius: 0.02rem;
                    }
                    .Jusda{
                        font-size: 0.18rem;color: #2E8EE2;border: 1px solid #2E8EE2;
                    }
                }
                .match{
                    .dispatching{
                        font-size: 0.2rem;color: gray;
                    }
                    .duration{
                        margin-left: 0.8rem;font-size: 0.23rem;
                        .kilometre{
                            color: gray;
                        }
                        .minute{
                            color: #3E7ECE;
                        }
                    }
                }
            }
        }
    }
  }
  footer{
      width: 100%;border-top: 1px solid rgb(233, 232, 232);position: fixed;
      bottom: 0;background: white;
    //   height:0.8rem;
    ul{
        display: flex;justify-content: space-around;
        list-style: none;text-align: center;margin-top: 0.1rem;
        li{
            width: 25%;
            i{
                font-size: 0.3rem;color: #475669;
            }
            .el-icon-time{
                font-size: 0.3rem;color: #2E8EE2;
              }
            a{
                text-decoration: none;
                color: gray;
                display: block;
            }
        }
    }
}
</style>


